<template>
    <div class="widgets personal-vision">
        <div class="title-widgets">
            <h2>{{data[lang].title}}</h2>
        </div>
        <p class="content">{{data[lang].content}}</p>
    </div>
</template>

<script>
export default {
    name: 'personal-vision',
    props: {
        lang: {
            type: String,
            default: 'en'
        }
    },
    data() {
        return {
            data: {
                en: {
                    title: 'Personal Vision',
                    content: 'I am young, enthusiastic, open-hearted and responsible boy who is looking for opportunities to make an impact on society, develop my communication skills, get experience and be a good Software Engineers.'
                },
                vi: {
                    title: 'Tầm nhìn cá nhân',
                    content: 'Bản thân là một người trẻ tuổi, đầy nhiệt huyết, cở mở và có trách nhiệm, tôi đang tìm kiếm cơ hội tạo sức ảnh hưởng đến xã hội, phát triển kĩ năng giao tiếp, kinh nghiệm để trở thành một kĩ sư phần mềm giỏi.'
                }
            }
        }
    }
}
</script>

<style scoped>
    .personal-vision .content {
        line-height: 24px;
    }
</style>



